<template>
    <div>
        <basicProps 
            :title="myTitle"
        />

        <basicEmit 
            @changeTitle="handleChange"
        />

        <sample-table
            :dataModel="currencies"
        />
    </div>
</template>

<script>
import basicProps from '@/components/01basicProps.vue'
import basicEmit from '@/components/02basicEmit.vue'
import sampleTable from '@/components/03sampleTable.vue'
import { binanceService } from '@/services'

export default {
    components: {
        basicProps,
        basicEmit,
        sampleTable,
    },
    data() {
        return {
            myTitle: "Hello worlds",
            currencies: [],
        }
    },
    async mounted()  {
        await this.callApiGet()
    },
    methods: {
        handleChange(e){
            this.myTitle = e.target.value 
        },
        async callApiGet(){
            const response = await binanceService.getInformation()
            this.currencies = response.data
        }
    },
}
</script>
